@import "src/common/Styles/Sass/variables.scss";
@import "src/common/Styles/Sass/mixins";

.title {
    margin-bottom: 4rem;
    position: relative;
    @include text($mainFontFamily, 6rem, 800, 3rem, $primaryColor);
    h2 {
        font-size: 3rem;
        margin-bottom: 0.5rem;
        line-height: 1.2;
        margin-top: 0;
    }
    &::after {
        position: absolute;
        content: '';
        width: 40px;
        height: 5px;
        bottom: -15px;
        left: 50%;
        background-color: $secondaryColor;
        transform: translate(-50%, 50%);
    }
}


